header {
    height: 70vh;
    padding-top: 7rem;
    
}

.about__container {
    display: grid;
    margin-top: 0.9rem;
    background: rgba(0, 0, 0, 0.3);
    padding: 3rem;
    border-radius: 3rem;
    opacity: 90%; 
}

.about__container:hover {
    opacity: 100%;
}

.about_text_big {
    font-size: 40px;
}

.about_text_med {
    font-size: 25px;
    margin-bottom: 0.9rem;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.header__title {
    font-size: 100px;
    
}

/*CTA*/

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* HEADER SOCIALS*/

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;


}

.header__socials::after {
    content: '';
    width: 2px;
    height: 2rem;
    background: var(--color-primary);
}

/* IMAGE */



/* SCROLL DOWN */

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 200;
    font-size: 0.9rem;
}

/* MEDIA QUERIES (MEDIUM DEVICES) */

@media screen and (max-width: 1024px) {
    header{
        height: 80vh;
    }
    .about_text_big {
        font-size: 25px;
        margin-bottom: 0.9rem;
    }
    
    .about_text_med {
        font-size: 20px;
        margin-bottom: 0.9rem;
    }
  }
  
  /* MEDIA QUERIES (SMALL DEVICES) */
  
  @media screen and (max-width: 600px) {
      header {
          height: 100vh;
      }

      .header__socials, .scroll__down {
          display: none;
      }
  }