.about__container {
    display: grid;
    margin-top: 0.9rem;
    background: rgba(0, 0, 0, 0.3);
    padding: 3rem;
    border-radius: 3rem;
    opacity: 60%; 
}

.about_text_big {
    font-size: 40px;
}

.about_text_med {
    font-size: 25px;
    margin-bottom: 0.9rem;
}

/* MEDIA QUERIES (MEDIUM DEVICES) */

@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
        
    }


    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
        
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
        
    }

    .about_text_big {
        font-size: 25px;
        margin-bottom: 0.9rem;
    }
    
    .about_text_med {
        font-size: 20px;
        margin-bottom: 0.9rem;
    }
    
}


    
/* MEDIA QUERIES (SMALL DEVICES) */

@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1,5rem 0;
    }
}